import  Vue                 from 'vue'
import  Vuex                from 'vuex'
import  * as getters        from './getters'
import  * as actions        from './actions'
import  * as mutations      from './mutations'

Vue.use(Vuex)
// 
const state = {
    deviceInfo: {}                                       // 
    ,city: {                                             // 
        cityCode: '3702'
        ,cityLongitude: '120.292284'                    //  - 
        ,cityLatitude:  '36.126949'                     //  - 
    }
    ,loadingState: {
        judgeShow: true
    }                                                   // 
    //  ''
    ,globalToggle: {
        houseResourceRightDrawer: {                     // '' -  - 
            open    : false
            ,docked : true
        }
    }
    ,bannerImg_Arr: []                                  // "" - banner
    ,hotBuildingList_Arr: []                            // "" - ""
    ,hotBuildingArr: []                                 // [0]
    ,buildingNum: []                                    // 
    ,furnitureInfo: {
        appPic: '',
        code: '',
        description: '',
        furnitureSizeList: [],
        imgUrl: [],
        name: '',
        price: 0,
        type: '',
        judgeShow: true
    }
    ,researchInfo: {                                    // "" -
        IPList: [],
        address: '',
        appPic: '',
        description: '',
        name: '',
        judgeShow: true
    }
    ,officeBuilding: {                                  // "" -
        judgeShow: true,
        buildingRelationList_judgeShow: true
    }
    ,coWorking: {                                       // "" - 
        judgeShow: true,
        buildingRelationList_judgeShow: true,
        nameInfo: '0'
    }
    //  ''   
    ,map: {
        // 
        searchParameter: {
            // cityCode                : '3702'             // Code( , cityCode )
            regionCode              : ''                    // Code
            ,businessCircleCode     : ''                    // Code
            ,bType                  : ''                    // (  )
            ,priceDayMin            : '0'                   //  - 
            ,priceDayMax            : ''                    //  - 
            ,priceMonthMin          : '0'                   //  - 
            ,priceMonthMax          : ''                    //  - 
            ,decoration             : ''                    // ( A:; B:; C:; D: )
        }
        // 
        ,mapState: {
            zoomNum                 : '11'                  // (  )
            ,indexLevel             : 'administrative'      // : administrative; : business; : building
            ,openMapNum             : []                    //  ''  - 
            ,changeIndexLevelNum    : []                    //  ''  - 
            ,requestNum             : []                    //  ''  - (  )
            ,resultNum              : []                    //  (  )
        }
        // 
        ,resultArr                  : []                    // (  +  +  )
    }
}
const store = new Vuex.Store({
    state,
    getters,
    actions,
    mutations,
})
if (module.hot) {
    module.hot.accept([
        './getters',
        './actions',
        './mutations',
    ], () => {
        store.hotUpdate({
            getters: require('./getters'),
            actions: require('./actions'),
            mutations: require('./mutations'),
        })
    })
}

export default store



